<template>
  <v-container>
    <v-card class="mx-auto mt-12">
      <v-card-title>Employees</v-card-title>
      <Employees
        v-for="employee in employees"
        :employee="employee"
        :key="employee.id"
      />
    </v-card>
    <v-btn
      color="red"
      fab
      dark
      small
      fixed
      bottom
      right
      class="mb-5"
      to="/new-employee"
    >
      <v-icon title="Add Employee">mdi-plus</v-icon>
    </v-btn>
  </v-container>
</template>

<script>
import db from "@/components/firebaseinit.js";
import Employees from "../components/Employees";
export default {
  name: "Dashboard",
  components: {
    Employees
  },
  data() {
    return {
      employees: []
    };
  },
  created() {
    db.collection("employees")
      .orderBy("dept")
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          const data = {
            id: doc.id,
            employee_id: doc.data().employee_id,
            dept: doc.data().dept,
            name: doc.data().name,
            position: doc.data().position
          };
          this.employees.push(data);
        });
      });
  }
};
</script>

<style scoped></style>
